<template>
  <div class="cropper-wrapper">
    <!-- <template v-if="!isPreview">
      <el-upload
        class="upload-demo"
        action=""
        drag
        :auto-upload="false"
        :show-file-list="false"
        :on-change="handleChangeUpload"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"></div>
        <div class="el-upload__tip">
          5MB
        </div>
      </el-upload>
    </template> -->
    <el-image
      v-if="avatarURL"
      :src="currentImg"
      class="blog-user--avatar"
      fit="cover"
    />
    <div class="pre-box">
      <el-upload
        action=""
        :auto-upload="false"
        :show-file-list="false"
        :on-change="handleChangeUpload"
      >
        <el-button type="primary" plain style="margin-left: 30px"
          ></el-button
        >
      </el-upload>
    </div>
    <el-button
      type="primary"
      plain
      style="margin-left: 10px"
      @click="handleModifyOrigin"
      ></el-button
    >
    <!-- vueCropper -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      class="crop-dialog"
      append-to-body
    >
      <div class="cropper-content">
        <div class="cropper" style="text-align: center">
          <VueCropper
            ref="cropper"
            :mode="option.mode"
            :img="option.img"
            :outputSize="option.size"
            :outputType="option.outputType"
            :info="option.info"
            :full="option.full"
            :canMove="option.canMove"
            :canMoveBox="option.canMoveBox"
            :original="option.original"
            :autoCrop="option.autoCrop"
            :fixed="option.fixed"
            :fixedNumber="option.fixedNumber"
            :centerBox="option.centerBox"
            :infoTrue="option.infoTrue"
            :fixedBox="option.fixedBox"
            :autoCropWidth="option.autoCropWidth"
            :autoCropHeight="option.autoCropHeight"
            @cropMoving="cropMoving"
          />
        </div>
      </div>
      <!-- <el-upload
        class="upload-demo"
        action=""
        :auto-upload="false"
        :show-file-list="false"
        :on-change="handleChangeUpload"
      >
      </el-upload> -->
      <p>: </p>
      <div class="action-box">
        <el-button
          type="primary"
          plain
          icon="el-icon-refresh-left"
          @click="rotateLeftHandle"
        />
        <el-button
          type="primary"
          plain
          icon="el-icon-refresh-right"
          @click="rotateRightHandle"
        />
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="finish" :loading="loading"
          ></el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
//vuecropper 
import { VueCropper } from 'vue-cropper'
import instance from '@/api/httpBase'

export default {
  name: 'VueBlogUserAvatarUpload',
  components: {
    VueCropper
  },
  props: {
    avatarURL: {
      type: String
    }
  },
  data() {
    return {
      isPreview: false,
      dialogVisible: false,
      previewImg: '', // 
      // option
      option: {
        img: '', // 
        mode: 'cover', //
        info: false, // 
        outputSize: 1, // 
        outputType: 'png', // 
        canScale: true, // 
        autoCrop: true, // 
        canMoveBox: true, // 
        autoCropWidth: '300', // 
        autoCropHeight: '300', // 
        fixedBox: false, //  
        fixed: true, // 
        fixedNumber: [1, 1], // 
        full: false, // 
        original: false, // 
        centerBox: true, // 
        infoTrue: false // true  false 
      },
      // 
      loading: false
    }
  },
  computed: {
    //||
    currentImg() {
      return this.previewImg || this.avatarURL
    }
  },
  methods: {
    // 
    uploadStart(url) {
      this.loading = false
      this.dialogVisible = true
      this.option.img = url
    },

    //blob
    async getUrlBlob(url) {
      url = process.env.VUE_APP_PROXY + url.match(/(\/user.*)/)?.[0]
      let resBlob = await instance.get(url, {
        responseType: 'blob' // blob
      })
      let blobURL = URL.createObjectURL(resBlob)
      return blobURL

      // BASE64
      // let img = new Image()
      // img.src = url
      // img.onload = () => {
      //   let canvas = document.createElement('canvas') //canvas DOM
      //   canvas.width = img.width
      //   canvas.height = img.height
      //   let ctx = canvas.getContext('2d')
      //   ctx.drawImage(img, 0, 0, img.width, img.height)
      //   let ext = url.match(/\.([a-z\d]+)$/)[1] || 'jpg'
      //   let dataURL = canvas.toDataURL('image/' + ext)
      //   canvas = null
      //   return dataURL //Base64
      // }
    },

    // 
    async handleModifyOrigin() {
      let dataURL = await this.getUrlBlob(this.avatarURL)
      this.uploadStart(dataURL)
    },

    // 
    beforeAvatarUpload(file) {
      let errMsg
      let isImg = /^image\/(jpeg|png|gif|bmp)$/.test(file.type)
      let isSize = file.size < 5 * 1024 * 1024

      if (!isImg) {
        errMsg = ' JPG/PNG/GIF/BMP !'
      }
      if (!isSize) {
        errMsg = '5M !'
      }

      if (!isImg || !isSize) {
        this.$notify.error({
          title: '',
          message: errMsg
        })
      }

      return isImg && isSize
    },

    // 
    handleChangeUpload(file) {
      let flag = this.beforeAvatarUpload(file.raw)
      if (!flag) return false

      // 
      this.$nextTick(async () => {
        // base64
        // this.option.img = await fileByBase64(file.raw)
        this.uploadStart(URL.createObjectURL(file.raw))
      })
    },

    // 
    rotateLeftHandle() {
      this.$refs.cropper.rotateLeft()
    },

    // 
    rotateRightHandle() {
      this.$refs.cropper.rotateRight()
    },

    // 
    // downloadHandle(type) {
    //   let aLink = document.createElement('a')
    //   aLink.download = 'author-img'
    //   if (type === 'blob') {
    //     this.$refs.cropper.getCropBlob((data) => {
    //       aLink.href = URL.createObjectURL(data)
    //       aLink.click()
    //     })
    //   } else {
    //     this.$refs.cropper.getCropData((data) => {
    //       aLink.href = data
    //       aLink.click()
    //     })
    //   }
    // },

    // 
    clearImgHandle() {
      this.option.img = ''
    },

    // 
    cropMoving(data) {
      //  xyxy
      // let cropAxis = [data.axis.x1, data.axis.y1, data.axis.x2, data.axis.y2]
      // console.log(cropAxis)
    },

    //()
    finish() {
      //  blob 
      let formData = new FormData()
      this.$refs.cropper.getCropBlob((blob) => {
        //blobformData 
        formData.append('user', blob)
        this.httpHandle(formData)

        this.loading = true
        this.dialogVisible = false
        this.previewImg = URL.createObjectURL(blob)
        this.isPreview = true
      })
      //  base64 
      // this.$refs.cropper.getCropData(data => {
      //     console.log(data)
      // })
    },

    async httpHandle(formData) {
      try {
        let { fileURL } = await this.$http({
          type: 'uploadAvatar',
          data: formData
        })
        //
        this.$bus.$emit('changeAvatar', fileURL)
      } catch (err) {
        this.$notify.error({
          title: '',
          message: ''
        })
      }
    }
  }
}
</script>

<style lang="stylus">
.blog-user--avatar
  width 150px
  height 150px
  border-radius 50%

.cropper-wrapper
  display flex
  align-items center

.crop-dialog
  .cropper-content
    padding 0 40px
    &+p
      padding 10px 0
      text-align center
  .cropper
    width auto
    height 350px
  .action-box
    padding 10px 0
    display flex
    justify-content center
    button
      font-size 18px
      margin-left 20px
  .dialog-footer
    button
      width 100px
</style>