export default {
  // trigger input pattern 
  username(formType) {
    const rules = (formType !== 'login') ? [
      { required: true, message: '', trigger: 'blur' },
      { pattern: /^(?!\d+$)(?![A-Za-z]+$)[\d\w]{6,10}$/, message: '6-10', trigger: 'blur' },
    ] : [{ required: true, message: '', trigger: 'blur' }]
    return rules
  },
  password(formType) {
    const rules = (formType !== 'login') ? [
      { required: true, message: '', trigger: 'blur' },
      { pattern: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[\w\d!@#$%^&*().]{8,18}$/, message: '8-18', trigger: 'blur' },
    ] : [{ required: true, message: '', trigger: 'blur' }]
    return rules
  },
  email() {
    return [
      { required: true, message: '', trigger: 'blur' },
      { type: 'email', message: '', trigger: 'blur' },
    ]
  },
  //
  name() {
    return [
      { required: true, message: '', trigger: 'blur' },
    ]
  },
  nickname() {
    return [
      { required: true, message: '', trigger: 'blur' },
      { pattern: /^[\da-zA-Z\u0391-\uFFE5]{1,8}$/, message: '1-8//', trigger: 'blur' },
    ]
  },
  signature() {
    return [
      { required: true, message: '', trigger: 'blur' },
      { pattern: /^.{1,30}$/, message: ' 1~30 ', trigger: 'blur' },
    ]
  },
}

